import PressPopover from 'press-ui/press-popover/press-popover.vue';
import PressCell from 'press-ui/press-cell/press-cell.vue';


export default {
  i18n: {
    'zh-CN': {
      options: num => `选项${num}`,
    },
    'en-US': {
      options: num => `Options ${num}`,
    },
  },
  components: {
    PressPopover,
    PressCell,
  },
  data() {
    return {
      list: ['Top', 'Bottom', 'Left', 'Right'],
      dataList: [1, 2, 3],
      direction: '',
      show: false,

    };
  },
  methods: {
    onShowPopover(direction) {
      this.direction = direction.toLowerCase();
      this.show = true;
    },
    onClickPage() {
      if (this.direction) {
        this.show = false;
      }
    },
    noop() {},
  },
};
